.myNameWrapper {
  height: 100px;
  width: 25%;
  position: relative;
  display: flex;
  flex-direction: column-reverse;
  margin: 150px 65% 60px auto;
}

.name {
  top: 1%;
  left: -1000%;
  color: #f5f5f5;
  position: absolute;
  animation: displayJobName 0.8s forwards;
  animation-delay: 0.8s;
  font-family: "Montserrat", sans-serif;
  white-space: nowrap;
  font-weight: 300;
  font-size: 65px;
}

.job {
  left: -1000%;
  position: absolute;
  color: rgb(98, 115, 255);
  animation: displayJobName 0.8s forwards;
  animation-delay: 1s;
  font-family: "Roboto", sans-serif;
  white-space: nowrap;
  font-weight: 100;
  font-size: 20px;
}

@keyframes displayJobName {
  0% {
    opacity: 0.1;
    left: -1000%;
  }
  80% {
    left: 25%;
    transform: rotateX(180deg);
    opacity: 0.2;
  }
  97% {
    opacity: 0.4;
  }
  99% {
    opacity: 0.8;
  }
  100% {
    transform: rotateX(360deg);
    left: 0;
  }
}/*# sourceMappingURL=MyName.css.map */